<template>
    <div class="signup">
        <v-container
            fluid
            px-4
            py-0
        >
            <img
                class="signup__logo"
                src="../../../images/logo_3.png"
                alt=""
            >
            <v-form
                lazy-validation
                @submit.prevent="onSubmit"
            >
                <div class="headline">
                    
                </div>
                <div
                    class="form-group"
                    :class="{invalid: $v.name.$error}"
                >
                    <v-text-field
                        v-model="name"
                        label=":"
                        :error-messages="nameErrors"
                        @blur="$v.name.$touch()"
                        @input="$v.name.$touch()"
                    />
                </div>

                <div
                    class="form-group"
                    :class="{invalid: $v.login.$error}"
                >
                    <v-text-field
                        v-model="login"
                        label=" :"
                        :error-messages="loginErrors"
                        @blur="$v.login.$touch()"
                        @input="$v.login.$touch()"
                    />
                </div>

                <div
                    class="form-group"
                    :class="{invalid: $v.email.$error}"
                >
                    <v-text-field
                        v-model="email"
                        label=" e-mail:"
                        :error-messages="emailErrors"
                        @blur="$v.email.$touch()"
                        @input="$v.email.$touch()"
                    />
                </div>

                <div
                    class="form-group"
                    :class="{invalid: $v.password.$error}"
                >
                    <v-text-field
                        v-model="password"
                        type="password"
                        :error-messages="passwordErrors"
                        label=":"
                        @blur="$v.password.$touch()"
                        @input="$v.password.$touch()"
                    />
                </div>
                <div
                    class="form-group"
                    :class="{invalid: $v.confirmPassword.$error}"
                >
                    <v-text-field
                        v-model="confirmPassword"
                        type="password"
                        :error-messages="confirmPasswordErrors"
                        label="   :"
                        @blur="$v.confirmPassword.$touch()"
                        @input="$v.confirmPassword.$touch()"
                    />
                </div>
                <div class="spacer" />
                <v-btn
                    color="primary"
                    dark
                    width="280"
                >
                    
                </v-btn>
                <v-btn
                    outlined
                    color="primary"
                    dark
                    to="/signin"
                    width="280"
                >
                    
                </v-btn>
                <div class="spacer" />
            </v-form>
        </v-container>
    </div>
</template>

<script>
import {
    required, email, minLength, sameAs,
} from 'vuelidate/lib/validators';

export default {
    data() {
        return {
            name: '',
            login: '',
            email: '',
            password: '',
            confirmPassword: '',
        };
    },
    computed: {
        nameErrors() {
            let error = '';
            if (!this.$v.name.$dirty) return error;
            if (!this.$v.name.required) {
                error = ' ';
            }
            return error;
        },
        loginErrors() {
            let error = '';
            if (!this.$v.login.$dirty) return error;
            if (!this.$v.login.required) {
                error = ' ';
            }
            return error;
        },
        emailErrors() {
            let error = '';
            if (!this.$v.email.$dirty) return error;
            if (!this.$v.email.email) {
                error = 'E-mail   ';
            } else if (!this.$v.email.required) {
                error = ' email';
            }
            return error;
        },
        passwordErrors() {
            let error = '';
            if (!this.$v.password.$dirty) return error;
            if (!this.$v.password.required) {
                error = ' ';
            } else if (!this.$v.password.minLength) {
                error = '     6 ';
            }
            return error;
        },
        confirmPasswordErrors() {
            let error = '';
            if (!this.$v.confirmPassword.$dirty) return error;
            if (!this.$v.confirmPassword.required) {
                error = '   ';
            } else if (!this.$v.confirmPassword.sameAs) {
                error = '   ';
            }
            return error;
        },
    },
    validations: {
        email: {
            required,
            email,
        },
        name: {
            required,
        },
        login: {
            required,
            minLen: minLength(6),
        },
        password: {
            required,
            minLen: minLength(6),
        },
        confirmPassword: {
            sameAs: sameAs('password'),
        },
    },
    methods: {
        async onSubmit() {
            await this.$store.dispatch('signup', {
                name: this.name,
                login: this.login,
                email: this.email,
                password: this.password,
            });
            this.$router.replace('/products');
        },
    },
};
</script>

<style>
    .signup {
        text-align: center;
    }

    .signup__label {
        display: block;
        margin-bottom: 5px;
        text-align: left;
    }

    .signup__logo {
        width: 145px;
    }

    .signup__form input {
        width: 100%;
        border: 1px solid #d1cece;
        padding: 10px;
        border-radius: 25px;
    }
    .invalid {
        color: red;
    }

</style>
